/* eslint-disable @typescript-eslint/no-explicit-any */
"use client"

import { useState, useEffect } from "react"
import { AppSidebar } from "@/components/app-sidebar"
import { DataTable } from "@/components/data-table"
import { SiteHeader } from "@/components/site-header"
import { SidebarInset, SidebarProvider } from "@/components/ui/sidebar"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Button } from "@/components/ui/button"
import { ColumnDef } from "@tanstack/react-table"
import { IconSearch, IconFilter, IconDownload } from "@tabler/icons-react"
import { LoadingDialog } from "@/components/ui/loading-dialog"
import { Badge } from "@/components/ui/badge"
import { format } from "date-fns"
import { tr } from "date-fns/locale"
import { subDays } from "date-fns"
import initSendRequest from "@/configs/sendRequest"
import { SimpleDateRangePicker } from "@/components/ui/simple-date-range-picker"

// DateRange type definition
type DateRange = {
  from?: Date
  to?: Date
}

interface OrderHistoryItem {
  historyId: number
  orderId: number
  accountId: number
  equityId: number
  orderStatus: "COMPLETED" | "PENDING" | "CANCELED" | "REJECTED" | "PROCESSING" | "PARTIALLY_FILLED"
  orderSide: "BUY" | "SELL"
  orderType: "MARKET" | "LIMIT"
  oldOrderQuantity: number
  oldPrice: number
  transactionTime: string
}

export default function OrderHistory() {
  const sendRequest = initSendRequest()

  const [loading, setLoading] = useState(false)
  const [orders, setOrders] = useState<OrderHistoryItem[]>([])
  const [filteredOrders, setFilteredOrders] = useState<OrderHistoryItem[]>([])

  const [searchQuery, setSearchQuery] = useState("")
  const [orderSideFilter, setOrderSideFilter] = useState<string | undefined>("all")
  const [statusFilter, setStatusFilter] = useState<string | undefined>("all")
  const [dateRange, setDateRange] = useState<DateRange | undefined>({
    from: subDays(new Date(), 30),
    to: new Date()
  })
  const [pagination, setPagination] = useState({
    pageIndex: 0,
    pageSize: 10,
  })

  useEffect(() => {
    fetchOrderHistory()
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    applyFilters()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchQuery, orderSideFilter, statusFilter, dateRange, orders])

  const fetchOrderHistory = async () => {
    setLoading(true)
    try {
      const response = await sendRequest.get("/order-histories")
      setOrders(response.data.content)
      setFilteredOrders(response.data.content)
    } catch (err: any) {
      console.error("Failed to fetch order history:", err)
    } finally {
      setLoading(false)
    }
  }

  const applyFilters = () => {
    let filtered = [...orders]

    // Search filter
    if (searchQuery) {
      filtered = filtered.filter(order =>
        order.orderId.toString().includes(searchQuery.toLowerCase()) ||
        order.historyId.toString().includes(searchQuery.toLowerCase()) ||
        order.accountId.toString().includes(searchQuery.toLowerCase()) ||
        order.equityId.toString().includes(searchQuery.toLowerCase())
      )
    }

    // Order side filter
    if (orderSideFilter && orderSideFilter !== "all") {
      filtered = filtered.filter(order => order.orderSide === orderSideFilter)
    }

    // Status filter
    if (statusFilter && statusFilter !== "all") {
      filtered = filtered.filter(order => order.orderStatus === statusFilter)
    }

    // Date range filter
    if (dateRange?.from && dateRange?.to) {
      filtered = filtered.filter(order => {
        const orderDate = new Date(order.transactionTime)
        return orderDate >= dateRange.from! && orderDate <= dateRange.to!
      })
    }

    setFilteredOrders(filtered)
  }

  const resetFilters = () => {
    setSearchQuery("")
    setOrderSideFilter("all")
    setStatusFilter("all")
    setDateRange({
      from: subDays(new Date(), 30),
      to: new Date()
    })
  }

  const exportToCSV = () => {
    const headers = [
      "History ID",
      "Order ID",
      "Account ID",
      "Equity ID",
      "Order Side",
      "Order Type",
      "Quantity",
      "Price",
      "Total Value",
      "Transaction Time",
      "Status"
    ]

    const csvData = filteredOrders.map(order => [
      order.historyId,
      order.orderId,
      order.accountId,
      order.equityId,
      order.orderSide,
      order.orderType,
      order.oldOrderQuantity,
      order.oldPrice,
      (order.oldOrderQuantity * order.oldPrice).toFixed(2),
      format(new Date(order.transactionTime), "dd.MM.yyyy HH:mm", { locale: tr }),
      order.orderStatus
    ])

    const csvContent = [headers, ...csvData]
      .map(row => row.map(field => `"${field}"`).join(","))
      .join("\n")

    const blob = new Blob([csvContent], { type: "text/csv;charset=utf-8;" })
    const link = document.createElement("a")
    const url = URL.createObjectURL(blob)
    link.setAttribute("href", url)
    link.setAttribute("download", `order-history-${format(new Date(), "yyyy-MM-dd")}.csv`)
    link.style.visibility = "hidden"
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }

  const columns: ColumnDef<OrderHistoryItem>[] = [
    {
      accessorKey: "historyId",
      header: "History ID",
      cell: ({ row }) => <span className="font-medium">#{row.original.historyId}</span>,
    },
    {
      accessorKey: "orderId",
      header: "Order ID",
      cell: ({ row }) => <span className="font-medium">#{row.original.orderId}</span>,
    },
    {
      accessorKey: "accountId",
      header: "Account",
      cell: ({ row }) => <span className="text-sm">ACC-{row.original.accountId}</span>,
    },
    {
      accessorKey: "equityId",
      header: "Equity",
      cell: ({ row }) => <span className="text-sm">EQ-{row.original.equityId}</span>,
    },
    {
      accessorKey: "orderSide",
      header: "Side",
      cell: ({ row }) => {
        const side = row.original.orderSide
        return (
          <Badge variant={side === "BUY" ? "default" : "outline"} className={
            side === "BUY"
              ? "bg-green-100 text-green-800 hover:bg-green-100 hover:text-green-800"
              : "bg-red-100 text-red-800 hover:bg-red-100 hover:text-red-800"
          }>
            {side}
          </Badge>
        )
      },
    },
    {
      accessorKey: "orderType",
      header: "Type",
      cell: ({ row }) => {
        const type = row.original.orderType
        return (
          <Badge variant="secondary" className="bg-blue-100 text-blue-800 hover:bg-blue-100 hover:text-blue-800">
            {type}
          </Badge>
        )
      },
    },
    {
      accessorKey: "oldOrderQuantity",
      header: "Quantity",
      cell: ({ row }) => row.original.oldOrderQuantity.toLocaleString(),
    },
    {
      accessorKey: "oldPrice",
      header: "Price",
      cell: ({ row }) => `${row.original.oldPrice.toFixed(2)}`,
    },
    {
      accessorKey: "totalValue",
      header: "Total Value",
      cell: ({ row }) => `${(row.original.oldOrderQuantity * row.original.oldPrice).toFixed(2)}`,
    },
    {
      accessorKey: "transactionTime",
      header: "Transaction Time",
      cell: ({ row }) => {
        const date = new Date(row.original.transactionTime)
        return format(date, "dd.MM.yyyy HH:mm", { locale: tr })
      },
    },
    {
      accessorKey: "orderStatus",
      header: "Status",
      cell: ({ row }) => {
        const status = row.original.orderStatus
        let badgeClass = ""

        switch (status) {
          case "COMPLETED":
            badgeClass = "bg-green-100 text-green-800 hover:bg-green-100 hover:text-green-800"
            break
          case "PENDING":
            badgeClass = "bg-yellow-100 text-yellow-800 hover:bg-yellow-100 hover:text-yellow-800"
            break
          case "PROCESSING":
            badgeClass = "bg-blue-100 text-blue-800 hover:bg-blue-100 hover:text-blue-800"
            break
          case "PARTIALLY_FILLED":
            badgeClass = "bg-orange-100 text-orange-800 hover:bg-orange-100 hover:text-orange-800"
            break
          case "CANCELED":
            badgeClass = "bg-gray-100 text-gray-800 hover:bg-gray-100 hover:text-gray-800"
            break
          case "REJECTED":
            badgeClass = "bg-red-100 text-red-800 hover:bg-red-100 hover:text-red-800"
            break
          default:
            badgeClass = "bg-gray-100 text-gray-800 hover:bg-gray-100 hover:text-gray-800"
        }

        return (
          <Badge variant="outline" className={badgeClass}>
            {status}
          </Badge>
        )
      },
    },
  ]

  return (
    <div>
      <SidebarProvider
        style={
          {
            "--sidebar-width": "calc(var(--spacing) * 72)",
            "--header-height": "calc(var(--spacing) * 12)",
          } as React.CSSProperties
        }
      >
        <AppSidebar variant="inset" />
        <SidebarInset>
          <SiteHeader title="Order History" />
          <div className="flex flex-1 flex-col">
            <div className="@container/main flex flex-1 flex-col gap-2">
              <div className="container px-4 mx-auto py-4 md:py-6">
                {/* Filters */}
                <Card className="mb-6">
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                      <IconFilter className="h-5 w-5" />
                      Filters
                    </CardTitle>
                    <CardDescription>
                      Filter your order history by various criteria
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                      <div className="grid gap-2">
                        <Label htmlFor="search">Search</Label>
                        <div className="relative">
                          <IconSearch className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
                          <Input
                            id="search"
                            placeholder="Order ID or Account"
                            className="pl-8"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                          />
                        </div>
                      </div>
                      <div className="grid gap-2">
                        <Label>Date Range</Label>
                        <SimpleDateRangePicker
                          date={dateRange}
                          setDate={setDateRange}
                        />
                      </div>
                      <div className="grid gap-4 grid-cols-2">
                        <div className="grid gap-2">
                          <Label htmlFor="order-side">Order Side</Label>
                          <Select
                            value={orderSideFilter}
                            onValueChange={setOrderSideFilter}
                          >
                            <SelectTrigger id="order-side">
                              <SelectValue />
                            </SelectTrigger>
                            <SelectContent>
                              <SelectItem value="all">All Sides</SelectItem>
                              <SelectItem value="BUY">Buy</SelectItem>
                              <SelectItem value="SELL">Sell</SelectItem>
                            </SelectContent>
                          </Select>
                        </div>
                        <div className="grid gap-2">
                          <Label htmlFor="status">Status</Label>
                          <Select
                            value={statusFilter}
                            onValueChange={setStatusFilter}
                          >
                            <SelectTrigger id="status">
                              <SelectValue />
                            </SelectTrigger>
                            <SelectContent>
                              <SelectItem value="all">All Statuses</SelectItem>
                              <SelectItem value="COMPLETED">Completed</SelectItem>
                              <SelectItem value="PENDING">Pending</SelectItem>
                              <SelectItem value="PROCESSING">Processing</SelectItem>
                              <SelectItem value="PARTIALLY_FILLED">Partially Filled</SelectItem>
                              <SelectItem value="CANCELED">Canceled</SelectItem>
                              <SelectItem value="REJECTED">Rejected</SelectItem>
                            </SelectContent>
                          </Select>
                        </div>
                      </div>
                    </div>

                    <div className="mt-4 flex justify-between">
                      <Button variant="outline" onClick={resetFilters}>
                        Reset Filters
                      </Button>
                      <Button
                        variant="outline"
                        className="flex items-center gap-2"
                        onClick={exportToCSV}
                      >
                        <IconDownload className="h-4 w-4" />
                        Export to CSV
                      </Button>
                    </div>
                  </CardContent>
                </Card>

                {/* Order History Table */}
                <Card>
                  <CardHeader>
                    <CardTitle>Order History</CardTitle>
                    <CardDescription>
                      Showing {filteredOrders.length} orders
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <DataTable
                      columns={columns}
                      data={filteredOrders}
                      pagination={pagination}
                      setPagination={setPagination}
                      loading={loading}
                    />
                  </CardContent>
                </Card>
              </div>
            </div>
          </div>
        </SidebarInset>
      </SidebarProvider>
      {loading && <LoadingDialog isOpen={loading} />}
    </div>
  )
}